import React from "react";
import {connect} from "react-redux";
import {addToCart, setSelectedVariantOnRecommended} from "eshop/modules/simfree/actions/app";
import {OraButton} from "eshop/components/OraCommonComponents";

class ProductRecomendationOneProductView extends React.Component {
    constructor(props) {
        super(props);
    }

    componentDidUpdate(prevProps) {
        this._loadModules();
    }

    _renderColorComponent(phone) {
        return (
            <div className="opl-wiking-gallery-navigation opl-carousel-vertical-arrows opl-color-chooser u-w-100">
                {this._renderColorChooser(phone, this.props.products)}
            </div>
        )
    }

    _renderColorChooser(phone, products) {
        return phone.variantList.map((variant,index) => {
            return (
                <label className="opl-color-chooser__item opl-wiking__color-item u-block" key={"colorChooser"+index}>
                    <input type="radio" name={"rec_color_" + phone.productId}
                           value={variant.productId}
                           checked={variant.productId == this.findProductVariant(phone).productId}
                           onClick={this.props.setSelectedVariantOnRecommended.bind(this, variant, variant.colorDefinition[0].code, phone.productId, this.props.products)}/>
                    <span className="opl-color-chooser__color opl-color-chooser__color--magnum u-margin-center"
                          style={{background: variant && variant.colorDefinition && variant.colorDefinition[0] && variant.colorDefinition[0].cssCode && variant.colorDefinition[0].cssCode.replace(/\'/g, "")}}></span>
                </label>
            )
        }, this);
    }

    findProductVariant(product) {
        for (var i = 0; i < product.variantList.length; i++) {
            if (product.variantList[i].productId == this.props.selectedVariant
                || (!this.props.selectedVariant && product.variantList[i].productId == this.props.selectedVariant)) {
                return product.variantList[i];
            }
        }
        return product.variantList[0];
    }

    render() {
        let phone = this.props.phone;
        let index = this.props.index;
        if (!phone.variantList || phone.variantList == null || phone.variantList.length == 0) {
            return (<div></div>);
        }
        return (
            <div className="opl-carousel__recomended-item u-spacing-all" data-slick-index={index} aria-hidden="false">
                <a href={'/sklep/' + phone.manufacturerURL + '/' + phone.productURL + '/' + this.findProductVariant(phone).colorDefinition[0].name}
                   className="opl-product-box u-small-no-shadow u-padding-all u-small-no-margin">
                    <div className="l-row u-padding-all-l u-small-no-padding">
                        <div className="l-col l-col-small-6 l-col-medium-12 l-col-12  opl-product-box__section">
                            <div className="u-animate-height js-layout-fixer-group-1">
                                <div className="js-height-sensitive-element">
                                    <div className="l-row">
                                        <div
                                            className="l-col l-col-medium-3 l-col-3  u-padding-left-s u-padding-right-s u-small-hide u-medium-hide">
                                            {this._renderColorComponent(phone)}
                                        </div>
                                        <div
                                            className="l-col l-col-small-12 l-col-medium-9 l-col-9  u-small-block u-medium-block u-margin-center">
                                            <div className="opl-product-box__image-wrapper u-text-center">
                                                <span className="opl-product-box__image-content">
                                                    <img src={this.findProductVariant(phone).imageUrl} alt="phone"
                                                         className="cvx opl-product-box__image"/>
                                                </span>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="l-col l-col-small-6 l-col-medium-12 l-col-12  opl-product-box__section">
                            <div className="u-animate-height js-layout-fixer-group-2">
                                <div className="js-height-sensitive-element">
                                    <div className="l-row u-no-margin">
                                        <div className="l-col l-col-12">
                                            <p className="u-font-bold u-padding-top-l">{phone.name}</p>
                                            <p className="g-brand1-c u-font-bold">{this.findProductVariant(phone).devicePaymentsData.oneTimePayment.price} zł</p>
                                        </div>
                                        <div className="l-col l-col-small-12 l-col-medium-9 l-col-9  u-padding-top">
                                            <button className="o-btn opl-btn opl-btn--small u-w-100 u-small-w-auto"
                                                    onClick={(e) => {
                                                        e.preventDefault();
                                                        this.props.addToCart(this.findProductVariant(phone).productId, phone.bundleTemplateName)
                                                    }
                                                    }>
                                                     <span className="opl-ripple-box">
                                                         <span className="opl-ripple-circle"/>
                                                     </span>
                                                Do koszyka
                                            </button>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </a>
            </div>
        )
    }

    _loadModules() {

    }
}

const mapStateToProps = (state, ownProps) => ({
    selectedVariant: ownProps.phone.selectedVariant
});

const mapDispatchToProps = (dispatch) => ({
    addToCart: (productCode, bundleTemplateName) => dispatch(addToCart(productCode, bundleTemplateName)),
    setSelectedVariantOnRecommended: (selectedVariantCode, colorCode, productCode, products) => dispatch(setSelectedVariantOnRecommended(selectedVariantCode, colorCode, productCode, products))
});

const ProductRecomendationOneProduct = connect(mapStateToProps, mapDispatchToProps)(ProductRecomendationOneProductView);
export default ProductRecomendationOneProduct;